import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import {
    GetImageSourceFunction,
    ITooltipCell,
} from '../../../../types/table-cells';

export interface ITextWithImageCell extends ITooltipCell {
    imageSource?: string;
    text?: string;
    imageHeight?: number;
    isImageAtFront?: boolean;
    getImageSource?: GetImageSourceFunction;
    getRouterLink?: (cellInput: any, cellValue: string) => any[];
    isLinkDisabled?: (cellInput: any) => boolean;
}

interface InitParams extends ITextWithImageCell, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying a text with an image.
 */

@Component({
    tag: 'atui-text-image-cell',
    shadow: false,
})
export class AtuiTextImageCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;
    @State() imageSource: string;
    @State() imageHeight = 20;
    @State() text: string;

    init(params: InitParams): void {
        this.params = params;
        this.imageSource = params.imageSource;
        this.imageHeight = params.imageHeight || this.imageHeight;
        this.text = params.value || params.text;

        if (params.getImageSource && params.data) {
            this.imageSource = params.getImageSource(params.data);
        }
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    private updateCell(newValue: string) {
        this.params.value = newValue;
        this.text = newValue;
    }

    render() {
        return (
            <Host class="flex items-center overflow-hidden">
                <atui-tooltip
                    position="right"
                    is_visible={!!this.params.generateTooltip}
                >
                    <div
                        class="flex items-center"
                        slot="tooltip-trigger"
                    >
                        {this.params.isImageAtFront && this.imageSource && (
                            <img
                                src={this.imageSource}
                                class="mr-2"
                                style={{ height: `${this.imageHeight}px` }}
                            />
                        )}

                        <span class="truncate">{this.text}</span>

                        {!this.params.isImageAtFront && this.imageSource && (
                            <img
                                src={this.imageSource}
                                class="ml-2"
                                style={{ height: `${this.imageHeight}px` }}
                            />
                        )}
                    </div>

                    {this.params.generateTooltip && (
                        <span
                            slot="tooltip-content"
                            class={`leading-normal`}
                        >
                            {this.params.generateTooltip(this.params)}
                        </span>
                    )}
                </atui-tooltip>
            </Host>
        );
    }
}
